import {ElMessage} from 'element-plus';

import {
    SVG
} from '@svgdotjs/svg.js'
/**
 * @description 绘制FCNN的神经元
 * @param draw 绘图组对象
 * @param {Object} attr 属性对象
 * @param {Number} x x坐标
 */
function FCNN(draw, attr, x) {
    if (attr) {
        const units = draw.group(); // 创建一个新的组
        // 绘制第一个球
        const circle = units.circle(attr.unit.diameter);
        // 计算第一个球的纵坐标
        const cy = -(attr.unit.number / 2 - 1 / 2) * attr.unit.space;
        // 神经元填充
        circle.fill({
            color: attr.unit.color,
            opacity: attr.unit.opacity,
        });
        // 神经元边框
        circle.stroke({
            color: attr.border.color,
            opacity: attr.border.opacity,
            width: attr.border.width,
        });
        // 神经元位置
        circle.x(x).cy(cy);
        // 循环复制剩余的球
        for (let unit = 1; unit < attr.unit.number; unit++) {
            const circle_clone = circle.clone();
            circle_clone.attr({
                cy: cy + unit * attr.unit.space
            });
            circle_clone.addTo(units);
        }
    }
}

/**
 * @description 绘制层叠图
 * @param draw 绘图组对象
 * @param {Object} attr 属性对象
 * @param {Number} x x坐标
 */
function Cascade(draw, attr, x) {
    if (attr) {
        const cascade = draw.group();
        // 绘制第一层
        const layer = cascade.rect(attr.width, attr.height);
        layer.x(x).y(0);
        layer.fill({
            color: attr.color,
            opacity: attr.opacity,
        });
        layer.skew(0, attr.skew); // 2D变形

        // 迭代复制生成剩下的层
        const y = -attr.space * Math.tan(attr.skew / 180 * Math.PI);
        for (let index = 1; index < attr.number; index++) {
            const layer_clone = layer.clone();
            layer_clone.x(x + attr.space * index).y(y * index)
            layer_clone.addTo(cascade);
        }
    }
}

/**
 * @description 绘制line的线
 * @param draw 绘图组对象
 * @param {Object} attr 属性对象
 * @param {Object} FCNNAttr FCNN对象属性
 * @param {Number} x x坐标
 * @param {Number} layerSpace 层间距
 */
function line(draw, attr, FCNNAttr, x, layerSpace) {
    if (attr && FCNNAttr) {
        const lines = draw.group();
        const start_x = x - FCNNAttr.start.diameter / 2; // 开始位置为上一层的坐标位置
        const end_x = x + layerSpace + FCNNAttr.end.diameter / 2; // 结束位置为第二层坐标位置
        const start_y = -(FCNNAttr.start.number / 2 - 1 / 2) * FCNNAttr.start.space;
        const end_y = -(FCNNAttr.end.number / 2 - 1 / 2) * FCNNAttr.end.space;
        // 绘制第一条线
        const line = lines.line(start_x, start_y, end_x, end_y);
        // 设置格式
        line.stroke({
            color: attr.color,
            width: attr.width,
            opacity: attr.opacity,
        });
        // 起始位置循环
        for (let startUnit = 0; startUnit < FCNNAttr.start.number; startUnit++) {
            // 判断结束开始循环起点
            let endUnit_first = startUnit ? 0 : 1;
            // 结束位置循环
            for (let endUnit = endUnit_first; endUnit < FCNNAttr.end.number; endUnit++) {
                const line_copy = line.clone();
                line_copy.plot(
                    start_x,
                    start_y + FCNNAttr.start.space * startUnit,
                    end_x,
                    end_y + FCNNAttr.end.space * endUnit,
                );
                line_copy.addTo(lines);
            }
        }
    }
}

// 绘制arrow箭头
/**
 * @description 绘制arrow箭头
 * @param draw 绘图组对象
 * @param {Object} attr 属性对象
 * @param {Number} x x坐标
 * @param {Number} layerSpace 层间距
 */
function arrow(draw, attr, x, layerSpace) {
    if (attr) {
        const cx = x + layerSpace / 2; // 中心位置x坐标
        const axis = new Array([ // 坐标矩阵
            [cx - attr.width / 2, attr.height / 2],
            [cx + attr.width / 2 - attr.arrowWidth, attr.height / 2],
            [cx + attr.width / 2 - attr.arrowWidth, attr.height / 2 + attr.arrowHeight],
            [cx + attr.width / 2, 0],
            [cx + attr.width / 2 - attr.arrowWidth, -(attr.height / 2 + attr.arrowHeight)],
            [cx + attr.width / 2 - attr.arrowWidth, -attr.height / 2],
            [cx - attr.width / 2, -attr.height / 2],
        ]);
        const arrow = draw.polygon(axis);
        arrow.fill({
            color: attr.color,
            opacity: attr.opacity,
        });
    }
}

/**
 * @description 自动移动图的位置
 * @param draw 绘图组对象
 * @param {Element} box 容器
 */
function auto_transform(draw, box) {
    const draw_cx = draw.cx(); // 当前绘图区域中心x坐标
    // console.log(draw.x());
    draw.translate(box.clientWidth / 2 - draw_cx, box.clientHeight / 2);
    // 如果超出绘图区域自动缩放
    const draw_width = draw.width(); // 当前绘图区域宽度
    const draw_height = draw.height(); // 当前绘图区域高度
    if (draw_width > box.clientWidth || draw_height > box.clientHeight) {
        const width_rate = box.clientWidth / draw_width; // 宽度缩比
        const height_rate = box.clientHeight / draw_height; // 高度缩比
        draw.scale(width_rate < height_rate ? width_rate * 0.9 : height_rate * 0.9); // 选最大比例缩放
    }
}

/**
 * @description 绘制小窗口中的图形
 * @param {String} selector 选择器
 * @param {Object} attr 属性对象
 */
const PlotDialog = (selector, attr) => {
    // 获取到绘图区域
    const box = document.querySelector(selector);
    box.innerHTML = null; // 清空内容，重新绘制
    const svg = SVG().size("100%", "100%");
    const draw = svg.group(); // 所有组
    const draw_link = draw.group(); // link组
    const draw_layer = draw.group(); // layer组
    switch (attr.linkType) {
        case "line": {
            const FCNNAttr = {
                start: {
                    number: attr.FCNN.unit.number,
                    diameter: attr.FCNN.unit.diameter,
                    space: attr.FCNN.unit.space,
                },
                end: {
                    number: attr.FCNN.unit.number,
                    diameter: attr.FCNN.unit.diameter,
                    space: attr.FCNN.unit.space,
                }
            };
            line(draw_link, attr.line, FCNNAttr, 0, attr.layerSpace);
            break;
        }
        case "arrow":
            arrow(draw_link, attr.arrow, 0, attr.layerSpace);
            break;
        default:
            break;
    }
    switch (attr.style) {
        case "FCNN":
            FCNN(draw_layer, attr.FCNN, 0 + attr.layerSpace);
            break;
        case "Cascade":
            Cascade(draw_layer, attr.Cascade, 0 + attr.layerSpace);
            break;
        default:
            break;
    }
    auto_transform(draw, box);
    svg.addTo(box);
};

/**
 * @description 绘制整张图
 * @param {String} selector 选择器
 * @param {Object} attr 属性对象
 */
function PlotWindow(selector, attr) {
    // 获取到绘图区域
    const box = document.querySelector(selector);
    box.innerHTML = null; // 清空内容，重新绘制
    const svg = SVG().size("100%", "100%");
    const draw = svg.group(); // 所有组
    const draw_link = draw.group(); // link组
    const draw_layer = draw.group(); // layer组

    let x = 0; // 当前横坐标
    for (let index in attr) {
        const layerAttr = attr[index]; // 当前层
        if (index !== '0') {
            switch (layerAttr.linkType) {
                case "line": {
                    const startLayer = attr[index - 1]; // 上一层
                    const endLayer = layerAttr; // 当前层
                    if (startLayer.FCNN) { // 上一层定义了FCNN
                        const FCNNAttr = {
                            start: {
                                number: startLayer.FCNN.unit.number,
                                diameter: startLayer.FCNN.unit.diameter,
                                space: startLayer.FCNN.unit.space,
                            },
                            end: {
                                number: endLayer.FCNN.unit.number,
                                diameter: endLayer.FCNN.unit.diameter,
                                space: endLayer.FCNN.unit.space,
                            }
                        };
                        line(draw_link, endLayer.line, FCNNAttr, x, endLayer.layerSpace); // 此刻x是前一层的尾部坐标
                    } else { // 未定义FCNN
                        ElMessage({
                            message: startLayer.type + '_' + startLayer.id + '层与'
                                + endLayer.type + '_' + endLayer.id
                                + '层之间不可以使用‘线’来连接，我们已经默认的将两层之间的连接改为无连接',
                            type: 'warning',
                            showClose: true, // 显示关闭按钮
                            duration: 5000, // 5秒关闭
                            customClass: 'el-message-custom',

                        })
                    }

                    x += endLayer.layerSpace; // 修正x为下一层起始位置
                    break;
                }
                case "arrow":
                    arrow(draw_link, layerAttr.arrow, x, layerAttr.layerSpace); // 此刻x是前一层的尾部坐标
                    x += layerAttr.layerSpace; // 修正x为下一层起始位置
                    break;
                default:
                    x += layerAttr.layerSpace; // 修正x为下一层起始位置
                    break;
            }
        }
        switch (layerAttr.style) {
            case "FCNN": {
                const attr = layerAttr.FCNN;
                FCNN(draw_layer, attr, x);
                x += attr.unit.diameter; // 修正x坐标
                break;
            }
            case "Cascade": {
                const attr = layerAttr.Cascade;
                Cascade(draw_layer, attr, x);
                x += attr.width + (attr.number - 1) * attr.space;
                break;
            }
            default:
                break;
        }
    }
    auto_transform(draw, box);
    svg.addTo(box);
}

export default {
    PlotDialog,
    PlotWindow,
}
