<template>
    <view class="v-dialog-body">
        <view class="v-dialog-dialog">
            <view class="v-dialog-header">
                <text>{{ attribute.type }}_{{ attribute.id }}</text>
                <button class="PlotNN-iconButton" type="button" @click="this.$emit('clickClose')"><img
                    src="./icon/close.svg" alt="关闭"/></button>
            </view>
            <view class="v-dialog-content">
                <!-- 输入框组件 -->
                <view class="v-dialog-input-group">
                    <!-- 类型选择框 -->
                    <view class="v-dialog-input">
                        <label>种类</label>
                        <select v-model="attribute.type">
                            <option v-for="item in defaultAttr.type" :value="item.key" :key="item.key">{{ item.value }}
                            </option>
                        </select>
                        <label>样式</label>
                        <select v-model="attribute.style">
                            <!--                            <template v-for="item in defaultAttr.style">-->
                            <!--                                <option-->
                            <!--                                    v-if="!(item.key==='FCNN'&&attribute.type==='CNN')&&!(item.key==='Cascade'&&attribute.type==='DNN')"-->
                            <!--                                    :value="item.key" :key="item.key">{{ item.value }}-->
                            <!--                                </option>-->
                            <!--                            </template>-->
                            <option v-for="item in defaultAttr.style[attribute.type]" :key="item.key" :value="item.key">
                                {{ item.value }}
                            </option>
                        </select>
                    </view>
                    <!-- DNN & FCNN -->
                    <view class="v-dialog-input" v-if="attribute.type==='DNN'&&attribute.style==='FCNN'">
                        <label>单元数量</label>
                        <input type="number" v-model.number="attribute.FCNN.unit.number" step="1" min="1"/>
                        <label>直径</label>
                        <input type="number" v-model.number="attribute.FCNN.unit.diameter" step="1" min="1"/>
                        <label>背景颜色</label>
                        <input type="color" v-model="attribute.FCNN.unit.color"/>
                        <label>背景透明度</label>
                        <view>
                            <input type="range" v-model.number="attribute.FCNN.unit.opacity" min="0" step="0.01"
                                   max="1"/><span style="width: 2rem;">{{ attribute.FCNN.unit.opacity }}</span>
                        </view>
                        <label>单元间距</label>
                        <input type="number" v-model.number="attribute.FCNN.unit.space" min="1" step="1"/>
                        <!-- <hr /> -->
                        <label>边框颜色</label>
                        <input type="color" v-model="attribute.FCNN.border.color"/>
                        <label>边框透明度</label>
                        <view>
                            <input type="range" v-model.number="attribute.FCNN.border.opacity" min="0" step="0.01"
                                   max="1"/><span style="width: 2rem;">{{ attribute.FCNN.border.opacity }}</span>
                        </view>
                        <label>边框宽度</label>
                        <input type="number" v-model.number="attribute.FCNN.border.width" min="0" step="0.1"/>
                    </view>
                    <view class="v-dialog-input" v-if="attribute.type==='CNN'&&attribute.style==='Cascade'">
                        <label>层数</label>
                        <input type="number" v-model.number="attribute.Cascade.number" step="1" min="1"/>
                        <label>宽</label>
                        <input type="number" v-model.number="attribute.Cascade.width" step="1" min="1"/>
                        <label>高</label>
                        <input type="number" v-model.number="attribute.Cascade.height" step="1" min="1"/>
                        <label>偏移角</label>
                        <input type="number" v-model.number="attribute.Cascade.skew" step="1"/>
                        <label>背景颜色</label>
                        <input type="color" v-model="attribute.Cascade.color"/>
                        <label>背景透明度</label>
                        <view>
                            <input type="range" v-model.number="attribute.Cascade.opacity" min="0" step="0.01"
                                   max="1"/><span style="width: 2rem;">{{ attribute.Cascade.opacity }}</span>
                        </view>
                        <label>层间距</label>
                        <input type="number" v-model.number="attribute.Cascade.space" min="1" step="1"/>
                    </view>
                    <view class="v-dialog-input">
                        <label>与上一层的连接</label>
                        <select v-model="attribute.linkType">
                            <!--                            <option v-for="item in defaultAttr.linkType" :value="item.key" :key="item.key">-->
                            <!--                                {{ item.value }}-->
                            <!--                            </option>-->
                            <option v-for="item in defaultAttr.linkType[attribute.style]" :value="item.key"
                                    :key="item.key">
                                {{ item.value }}
                            </option>
                        </select>
                    </view>
                    <view class="v-dialog-input" v-if="attribute.linkType==='line'">
                        <label>连接线颜色</label>
                        <input type="color" v-model.lazy="attribute.line.color"/>
                        <label>连接线透明度</label>
                        <view>
                            <input type="range" v-model.number="attribute.line.opacity" min="0" step="0.01"
                                   max="1"/><span style="width: 2rem;">{{ attribute.line.opacity }}</span>
                        </view>
                        <label>连接线宽度</label>
                        <input type="number" v-model.number="attribute.line.width" min="1" step="0.1"/>
                    </view>
                    <view class="v-dialog-input" v-if="attribute.linkType==='arrow'">
                        <label>箭头颜色</label>
                        <input type="color" v-model.lazy="attribute.arrow.color"/>
                        <label>箭头透明度</label>
                        <view>
                            <input type="range" v-model.number="attribute.arrow.opacity" min="0" step="0.01"
                                   max="1"/><span style="width: 2rem;">{{ attribute.arrow.opacity }}</span>
                        </view>
                        <label>宽</label>
                        <input type="number" v-model.number="attribute.arrow.width" min="1" step="0.1"/>
                        <label>高</label>
                        <input type="number" v-model.number="attribute.arrow.height" min="1" step="0.1"/>
                        <label>箭头宽</label>
                        <input type="number" v-model.number="attribute.arrow.arrowWidth" min="1" step="0.1"/>
                        <label>箭头高</label>
                        <input type="number" v-model.number="attribute.arrow.arrowHeight" min="1" step="0.1"/>
                    </view>
                    <view class="v-dialog-input">
                        <label>与上一层间距</label>
                        <input type="number" v-model.number="attribute.layerSpace" min="1" step="1"/>
                    </view>
                </view>
                <view class="v-dialog-input-group v-dialog-right">
                    <view class="v-dialog-svg" id="svg-dialog" @wheel.capture.prevent="svgScale"
                          @mousedown.capture.prevent="svgMove" @mouseup.capture.prevent="svgMove">

                    </view>
                    <button type="button" class="PlotNN-layer-window-button" @click="save">保存</button>
                </view>
            </view>
        </view>
    </view>
</template>

<script>
import {SVG} from '@svgdotjs/svg.js'

const defaultAttr = require('./default.json');
import PlotNN from './PlotNN.js'
import detectZoom from '@/assets/js/detectZoom.js' // 计算网页缩放比例

export default {
    name: "v-dialog",
    props: {
        thisLayer: {
            type: Object,
            required: true,
        }
    },
    data() {
        return {
            attribute: this.thisLayer,
            defaultAttr,
        };
    },
    mounted() {
        this.attribute = JSON.parse(JSON.stringify(this.thisLayer)); // 深拷贝转换
    },
    watch: {
        'attribute.type': {
            handler: function (newdata) {
                this.attribute.style = this.defaultAttr.style[newdata][0].key; // 修正style
            },
        },
        'attribute.style': {
            handler: function (newdata) {
                if (!this.attribute[newdata]) { // 如果没有被定义过
                    this.attribute[newdata] = JSON.parse(JSON.stringify(this.defaultAttr[newdata])); // 深拷贝转换
                }
                // 同时载入linkType
                this.attribute.linkType = 'none';
            },
        },
        'attribute.linkType': {
            handler: function (newdata) {
                if (!this.attribute[newdata]) { // 如果没有被定义过
                    this.attribute[newdata] = JSON.parse(JSON.stringify(this.defaultAttr[newdata])); // 深拷贝转换
                }
            },
        },
        attribute: {
            handler: function (newdata) {
                PlotNN.PlotDialog('#svg-dialog', newdata);
            },
            deep: true, // 深度监听
        },
    },
    methods: {
        save() { // 保存
            const attribute = { // 取出输入的attribute
                id: this.attribute.id,
                type: this.attribute.type,
                style: this.attribute.style,
                linkType: this.attribute.linkType,
                layerSpace: this.attribute.layerSpace,
            }
            attribute[this.attribute.style] = this.attribute[this.attribute.style];
            attribute[this.attribute.linkType] = this.attribute[this.attribute.linkType];
            this.$emit('clickSave', attribute);
        },
        // svg的拖动和放大缩小
        svgScale(e) {
            const direction = e.deltaY > 0 ? 'down' : 'up'; //deltaY为正则滚轮向下，为负滚轮向上
            const svg = SVG("#svg-dialog>svg>g");
            if (svg) {
                if (direction === 'down' && e.deltaY >= 125 && svg.transform().scaleX >
                    0.1) { //125为用户一次滚动鼠标的wheelDelta的值
                    // 缩小
                    svg.scale(0.8);
                } else if (direction === 'up' && e.deltaY <= -125 && svg.transform().scaleX < 10) {
                    // 放大
                    svg.scale(1.25);
                }
            }

        },
        svgMousemove(e) { // 移动鼠标事件
            if (e.buttons === 1) { // 监测鼠标在移动的时候是否被按下
                const svg = SVG("#svg-dialog>svg>g");
                // if (svg) {
                svg.translate(e.movementX / this.zoom, e.movementY / this.zoom);
                // }
            } else {
                const element = document.getElementById('svg-dialog')
                element.removeEventListener('mousemove', this.svgMousemove);
            }
        },
        svgMove(e) { // 鼠标按下事件
            const element = document.getElementById('svg-dialog')
            if (e.type === "mousedown") {
                // 鼠标按下
                const svg = SVG("#svg-dialog>svg>g");
                if (svg) { // 判断是否有绘图
                    this.zoom = detectZoom(); // 更新缩放比例
                    element.addEventListener('mousemove', this.svgMousemove);
                }
            } else {
                // 鼠标抬起
                element.removeEventListener('mousemove', this.svgMousemove);
            }
        },
    }
}
</script>

<style>
.v-dialog-body {
    background-color: rgba(0, 0, 0, 0.3);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 998;
    justify-content: center;
    align-items: center;
}

.v-dialog-dialog {
    background-color: white;
    border-radius: 5px;
    height: 80%;
    width: 50%;
    min-width: 500px;
    z-index: 999;
    /* flex-wrap: wrap; */
    padding: 0 10px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}

.v-dialog-header {
    /* width: 100%; */
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
}

.v-dialog-header text {
    font-weight: normal;
}

.v-dialog-content {
    align-items: stretch;
    padding: 0.4rem;
    height: calc(100% - 3.4rem);
}

.v-dialog-input-group {
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 0;
    /* margin: 0.4rem; */
    /* height: calc(100% - 3.4rem); */
    overflow: auto;
}

.v-dialog-right {
    flex: 1;
}

.v-dialog-input {
    display: grid;
    grid-template-columns: auto auto;
    /*grid-template-rows: repeat(auto-fill, 2rem);*/
    justify-content: flex-start;
    align-content: flex-start;
    align-items: center;
    font-weight: lighter;
    gap: 0.5rem;
    border-bottom: #E8E8E8 solid 1px;
    padding: 0.5rem 0;
}

.v-dialog-input:last-child {
    border: none;
}

.v-dialog-input input,
.v-dialog-input select {
    height: 1.5rem;
    line-height: 1.5rem;
    border: #E8E8E8 solid 1px;
    border-radius: 5px;
    max-width: 5rem;
    /* margin: 0; */
}

.v-dialog-input select {
    padding: 1px;
    box-sizing: content-box;
}

.v-dialog-input view {
    justify-content: space-around;
    align-items: center;
}

.v-dialog-input view span {
    padding: 0 0.5rem;
}

/*    .v-dialog-input hr {
    height: 1px;
    border: none;
    width: 100%;
    background-color: #E8E8E8;
    grid-column-start: 1;
    grid-column-end: 3;
    margin: 0;
} */

.v-dialog-svg {
    /* width: 50%; */
    /* flex-shrink: 0; */
    flex-grow: 1;
    margin: 0.4rem;
    /* height: calc(100% - 3.4rem); */
    /* box-sizing: border-box; */
    overflow: hidden;
    /* box-shadow: 0 0 4px 1px #E8E8E8; */
    border: #E8E8E8 solid 1px;
    border-radius: 4px;
    cursor: move;
}
</style>
