<template>
    <view class="TXT_contain">
        <label>数据类型</label>
        <select v-model.number="file_type">
            <option value="0">多频点多参数</option>
        </select>
        <label>选择文件</label>
        <input type="file" @change="getFile" accept="text/plain"/>
        <label style="align-self: flex-start">预览</label>
<!--        <textarea readonly v-model="preview" cols="40" rows="30"></textarea>-->
        <view class="preview">
            <table>
                <tr><th>字段</th><th>数据量</th></tr>
                <tr v-for="(value, key) in array" :key="key">
                    <td>{{ key }}</td>
                    <td>{{ value.length }}</td>
                </tr>
            </table>
        </view>

        <label>下载csv</label>
        <a class="menu-a downloadCsv" ref="downloadCsv" href="#" @click="downloadCsv"><text>点击下载</text></a>
    </view>
</template>

<script>
import JSON2CSV from '@/assets/js/JSON2CSV.js'
import {ElMessage} from 'element-plus';
export default {
    name: "TXT",
    data() {
        return {
            file_type: 0,
            file_name: null,
            array: null,
        }
    },
    methods: {
        getFile(e) {
            // 检测是否为空
            if (e.target.files.length > 0) {
                const file = e.target.files[0];
                console.log(file);
                this.file_name = file.name;
                // 检测文件类型
                if (file.type === "text/plain") {
                    this.array = null; // 清空array内容
                    const reader = new FileReader();
                    reader.onload = this.readFileByLine;
                    reader.onerror = (e) => {
                        ElMessage({
                            message: "文件读取失败：" + JSON.stringify(e),
                            type: 'error',
                            showClose: true, // 显示关闭按钮
                            duration: 3000, // 3秒关闭
                            // customClass: 'el-message-custom',
                        })
                    }
                    ElMessage({
                        message: "正在读取文件：" + file.name,
                        type: 'info',
                        showClose: true, // 显示关闭按钮
                        duration: 3000, // 3秒关闭
                        // customClass: 'el-message-custom',
                    })
                    reader.readAsText(file);
                } else {
                    ElMessage({
                        message: "文件类型错误，只能为.txt文件",
                        type: 'warning',
                        showClose: true, // 显示关闭按钮
                        duration: 3000, // 3秒关闭
                        // customClass: 'el-message-custom',
                    })
                }
            }
        },
        readFileByLine(e) {
            const result = e.target.result; // 提取字符串结果
            if (result) {
                const groups = result.split(/\r\n\r\n/); // 切分空行
                let array = {};
                let index_header;
                for (let group_index = 0; group_index<groups.length; group_index++){
                    const group = groups[group_index]; // 取出这一组数据
                    if (group === "") {
                        console.log("读取结束，共读取%d行", group_index-1);
                        break;
                    }
                    const lines = group.split(/\r\n/); // 切分换行
                    let data_header
                    for (let line_index = 0; line_index < lines.length; line_index++) {
                        let line = lines[line_index].replace(/(^\s*)|(\s*$)/g, ""); // 去除开头和结尾的空格
                        if (group_index === 0 && line_index === 0) { // 第一组的第一行
                            line = line.split(/\s{2,}/); // 标题行划分
                            index_header = line[0].replace(/,/g, ' '); // 给标题头赋值
                            data_header = line[1].replace(/,/g, ' '); // 给数据头赋值
                            array[index_header] = []; // 初始化索引序列
                            array[data_header] = []; // 初始化索引序列
                        } else if (group_index === 0 && line_index > 1) { // 第一组的数据
                            line = line.split(/\s+/); // 数据行划分
                            array[index_header].push(parseFloat(line[0])); // 放入索引
                            array[data_header].push(parseFloat(line[1])); // 放入数据
                        } else if (line_index === 0) { // 第n组第一行
                            line = line.split(/\s{2,}/); // 标题行划分
                            data_header = line[1].replace(/,/g, ' '); // 给数据头赋值
                            array[data_header] = []; // 初始化索引序列
                        } else if (line_index > 1) { // 第n组的数据
                            line = line.split(/\s+/); // 数据行划分
                            array[data_header].push(parseFloat(line[1])); // 放入数据
                        }
                    }
                }
                // let preview = "";
                // for (let item in array) {
                //     preview += item + ": " + Object.prototype.toString.call(array[item]) + "(" + array[item].length + ")" + "\n";
                // }
                // this.preview = preview;
                this.array = array;
                ElMessage({
                    message: "读取文件成功！",
                    type: 'success',
                    showClose: true, // 显示关闭按钮
                    duration: 3000, // 3秒关闭
                    // customClass: 'el-message-custom',
                })
            } else {
                ElMessage({
                    message: "数据为空，请检查文件是否有错误",
                    type: 'warning',
                    showClose: true, // 显示关闭按钮
                    duration: 3000, // 3秒关闭
                    // customClass: 'el-message-custom',
                })
            }
        },
        downloadCsv(){
            if (this.array) {
                ElMessage({
                    message: "正在导出csv文件，这需要一定的时间",
                    type: 'info',
                    showClose: true, // 显示关闭按钮
                    duration: 5000, // 3秒关闭
                    // customClass: 'el-message-custom',
                })
                let blob = JSON2CSV.json2csv(this.array, true);
                const a = this.$refs.downloadCsv;
                // console.log(a);
                // a.href = 'data:application/octet-stream;base64,' + btoa(unescape(encodeURIComponent(svg)));
                a.href = URL.createObjectURL(blob);
                a.download = this.file_name.split('.')[0] + ".csv";
            } else {
                ElMessage({
                    message: "还未读取txt文件数据",
                    type: 'warning',
                    showClose: true, // 显示关闭按钮
                    duration: 3000, // 3秒关闭
                    // customClass: 'el-message-custom',
                })
            }
        }
    }
}
</script>

<style>
.TXT_contain {
    display: grid;
    grid-template-columns: auto auto;
    /*grid-template-rows: repeat(auto-fill, 2rem);*/
    justify-content: center;
    align-content: flex-start;
    align-items: center;
    font-weight: lighter;
    gap: 0.5rem;
    border-bottom: #E8E8E8 solid 1px;
    padding: 0.5rem 0;
}

.TXT_contain:last-child {
    border: none;
}

.TXT_contain input,
.TXT_contain select {
    height: 1.5rem;
    line-height: 1.5rem;
    border: #E8E8E8 solid 1px;
    border-radius: 5px;
    /*max-width: 5rem;*/
    /* margin: 0; */
}

.TXT_contain select {
    padding: 1px;
    box-sizing: content-box;
}

.TXT_contain input[type="file"] {
    border: none;
    border-radius: 0;
    height: auto;
}
.preview{
    overflow: auto;
    /*width: 300px;*/
    height: 300px;
    align-items: flex-start;
}
/*滚动条样式 chrome和safari*/
.preview::-webkit-scrollbar{
    width: 5px;
    /*height: 1px;*/
}

.preview::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius   : 5px;
    background-color: #808080;
}
.preview::-webkit-scrollbar-track{
    /*滚动条里面轨道*/
    /*box-shadow   : inset 0 0 5px rgba(0, 0, 0, 0.2);*/
    background   : #e8e8e8;
    /*border-radius: 5px;*/
}
.preview table{
    width: 500px;
    text-align: center;
    margin: 0;
    border-collapse: collapse;
    border-spacing: 0;
}
.preview td,
.preview th{
    margin: 0;
    border: 1px solid #e8e8e8;
}
.downloadCsv{
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>