/**
 * @description json转csv格式，仅仅转成字符串
 * @param {Object} data 数据，一个对象，每个属性是一列
 * @param {Boolean} header 是否添加标题行
 // * @param {Number} rows 行数
 * @return {Blob} CSV字符串
 */
function json2csv(data, header) {
    let CSV = '';
    let keys = Object.keys(data)
    if (header) { // 如果需要添加首行header
        let row = '';
        for (let key of keys) {
            row += key + ',';
        }
        row = row.slice(0, -1); // 删除最后一个,号，即a,b, => a,b
        CSV += row + '\r\n'; // 添加换行符号
    }
    let rows = data[keys[0]].length;
    // console.log(rows)
    for (let row_index = 0; row_index < rows; row_index++) { // 对csv中的行遍历
        let row = '';
        for (let column in data) { // 每一行的不同列
            row += data[column][row_index] + ',';
        }
        row = row.slice(0, -1); // 删除最后一个,号，即a,b, => a,b
        CSV += row + '\r\n'; // 添加换行符号
    }
    return new Blob(["\uFEFF"+CSV], {type: 'text/csv;charset=utf-8;'});
}

export default {
    json2csv
}