<template>
    <view class="PlotNN-body">
        <view class="PlotNN-layer-window">
            <view class="PlotNN-window-header">
                <text>层</text>
                <a ref="downloadSvg" class="menu-a" href="#" @click="downloadSvg"><text>下载图形</text></a>
            </view>
            <view class="PlotNN-layer-window-group">
                <draggable v-model="layers" tag="transition-group" item-key="id"
                           :component-data="{name: 'flip-list', type: 'transition' }"
                           handle=".PlotNN-layer-window-layer-drag">
                    <template #item="{element, index}">
                        <view class="PlotNN-layer-window-layer" :key="element.id">
                            <img class="PlotNN-layer-window-layer-drag" src="./icon/drag.svg" alt="拖动"/>
                            <text>{{ element.type }}_{{ element.id }}</text>
                            <button class="PlotNN-iconButton" type="button" @click="clickClone(index)" title="复制">
                                <img src="./icon/clone.svg" alt="复制"/>
                            </button>
                            <button class="PlotNN-iconButton" type="button" @click="clickEdit(index)" title="编辑">
                                <img src="./icon/edit.svg" alt="编辑"/>
                            </button>
                            <button class="PlotNN-iconButton" type="button" @click="clickDelete(index)" title="删除">
                                <img src="./icon/delete.svg" alt="删除"/>
                            </button>

                        </view>
                    </template>
                    <template #footer>
                        <button class="PlotNN-layer-window-button" type="button"
                                @click="clickAddLayer">
                            <text>添加一层</text>
                        </button>
                    </template>
                </draggable>
            </view>
        </view>
        <view class="PlotNN-svg-window" id="svg-window" @wheel.capture.prevent="svgScale"
              @mousedown.capture.prevent="svgMove" @mouseup.capture.prevent="svgMove">

        </view>
        <v-dialog v-if="showLayerDialog" :thisLayer="LayerDialog" @clickClose="clickCloseDialog"
                  @clickSave="clickSaveDialog"></v-dialog>
    </view>
</template>

<script>
import draggable from 'vuedraggable' // 可拖动组件
import vDialog from './v-dialog.vue' // 弹出框
const defaultAttr = require('./default.json') // 默认表单
import PlotNN from './PlotNN.js' // 绘图程序
import {SVG} from '@svgdotjs/svg.js' // svg.js 绘制svg的插件
import detectZoom from '@/assets/js/detectZoom.js' // 计算网页缩放比例
export default {
    name: "PlotNN",
    components: {
        draggable: draggable,
        "v-dialog": vDialog,
    },
    data() {
        return {
            layerID: 0,
            layers: [],
            showLayerDialog: false,
            LayerDialog: {},
            zoom: 1
        }
    },
    methods: {
        clickClone(index) { // 复制这一层
            // console.log(index);
            const item = JSON.parse(JSON.stringify(this.layers[index]));
            item.id = ++this.layerID;
            this.layers.push(item);
        },
        clickEdit(index) { // 编辑
            // console.log(index);
            this.LayerDialog = this.layers[index];
            this.showLayerDialog = true;
        },
        clickDelete(index) { // 删除一层
            this.layers.splice(index, 1); // 删除这一条
            // console.log(index);
        },
        clickCloseDialog() { // 关闭窗口
            // console.log(this.showLayerDialog);
            this.showLayerDialog = false;
        },
        clickAddLayer() { // 新增一层
            if (this.layers.length === 0) {
                this.LayerDialog = {
                    id: this.layerID + 1,
                    type: defaultAttr.type[0].key,
                    style: defaultAttr.style.DNN[0].key,
                    FCNN: defaultAttr.FCNN,
                    linkType: defaultAttr.linkType.FCNN[0].key,
                    line: defaultAttr.line,
                    layerSpace: defaultAttr.layerSpace,
                };
            } else {
                const item = this.layers[this.layers.length - 1];
                this.LayerDialog = JSON.parse(JSON.stringify(item));
                this.LayerDialog.id = this.layerID + 1;
            }
            this.showLayerDialog = true;
        },
        clickSaveDialog(e) { // 点击了窗口的保存事件
            // console.log(e);
            let item = this.layers.findIndex(function (element) {
                return element.id === e.id
            });
            // console.log(item);
            if (item === -1) { // 如果是添加模式
                // item = e;
                this.layers.push(e);
                this.layerID++; // id自加1
                // console.log(1);
            } else { // 编辑模式
                // item = item[0];
                // item = e;
                this.layers[item] = e;
                // console.log(2);
            }
            // console.log(this.layers);
            // this.layerID++; // id自加1
            this.showLayerDialog = false;
        },
        // svg的拖动和放大缩小
        svgScale(e) {
            const direction = e.deltaY > 0 ? 'down' : 'up'; //deltaY为正则滚轮向下，为负滚轮向上
            const svg = SVG("#svg-window>svg>g");
            if (svg) {
                if (direction === 'down' && e.deltaY >= 125 && svg.transform().scaleX >
                    0.1) { //125为用户一次滚动鼠标的wheelDelta的值
                    // 缩小
                    svg.scale(0.8);
                } else if (direction === 'up' && e.deltaY <= -125 && svg.transform().scaleX < 10) {
                    // 放大
                    svg.scale(1.25);
                }
            }

        },
        svgMousemove(e) { // 移动鼠标事件
            if (e.buttons === 1) { // 监测鼠标在移动的时候是否被按下
                const svg = SVG("#svg-window>svg>g");
                // if (svg) {
                svg.translate(e.movementX / this.zoom, e.movementY / this.zoom);
                // }
            } else {
                const element = document.getElementById('svg-window')
                element.removeEventListener('mousemove', this.svgMousemove);
            }
        },
        svgMove(e) { // 鼠标按下事件
            const element = document.getElementById('svg-window')
            if (e.type === "mousedown") {
                // 鼠标按下
                const svg = SVG("#svg-window>svg>g");
                if (svg) { // 判断是否有绘图
                    this.zoom = detectZoom(); // 更新缩放比例
                    element.addEventListener('mousemove', this.svgMousemove);
                }
            } else {
                // 鼠标抬起
                element.removeEventListener('mousemove', this.svgMousemove);
            }
        },
        downloadSvg(){
            const svg = SVG("#svg-window").node.innerHTML;
            console.log(svg);
            if (svg) {
                const a = this.$refs.downloadSvg;
                // console.log(a);
                let blob = new Blob([svg], {type:'image/svg+xml'});
                // a.href = 'data:application/octet-stream;base64,' + btoa(unescape(encodeURIComponent(svg)));
                a.href = URL.createObjectURL(blob);
                a.download = "QuickScript-PlotNN.svg";
            }
        }
    },
    watch: {
        layers: {
            handler: function (newdata) {
                PlotNN.PlotWindow('#svg-window', newdata);
            },
            deep: true, // 深度监听
        }
    }
}
</script>

<style>
@import "PlotNN.css";
@import "message.css";

/* 动态效果 */
.flip-list-move {
    transition: transform 0.5s;
}

.no-move {
    transition: transform 0s;
}
</style>
